video{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
}


.hero-container{
    /* background: url('../pexels-pixabay-417344.jpg') center center/cover no-repeat;  */
    text-align: center;
     height:100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;

}

.hero-container >h1{
color:white;
font-size: 100px;
margin-top: -100px;
}

.hero-container >p{
    margin-top: 8px;
    color: white;
    font-size: 32px;
}

.hero-btns{
    margin-top: 32px;
}

.hero-btns .btn{
    margin: 6px;
}


@media screen and (max-width:960px){
    .hero-container >h1{
        color:white;
        font-size: 70px;
        margin-top: -150px;
        }
        
        .hero-container >p{
            margin-top: 5px;
            color: white;
            font-size: 25px;
        }
}

@media screen and (max-width:768px){
    .hero-container >h1{
        color:white;
        font-size: 50px;
        margin-top: -100px;
        }
        
        .hero-container >p{
            margin-top: 8px;
            color: white;
            font-size: 20px;
        }

        .btn-mobile{
            display: block;
            text-decoration: none;
        }

        .btn{
            width: 100%;
        }

}